import React from 'react';
import { Link } from 'react-router-dom'
import './Navbar.css'
import logo from './logo.svg'
import {useState} from 'react'
import {Icon} from 'react-icons-kit'
import {menu} from 'react-icons-kit/feather/menu'
import {x} from 'react-icons-kit/feather/x'

const Navbar = () => {


  const [toggle, setToggle]=useState(false);

  const handleToggle=()=>{
    setToggle(!toggle);
  }



  return (
    <div class={toggle?'navbar expanded':'navbar'}>
      <div className='nav-logo'>

        <img src={logo} alt='img' style={{width:"47px",height:"47px"}} />
     
        <h2 className='logo'>KEC</h2>

      </div>
      <div className='toggle-icon' onClick={handleToggle}>
          {toggle?<Icon icon={x} size={28}/>:<Icon icon={menu} size={28}/>}

      </div>
 
      <div class="navbar-menu">
        <Link class="navbar-item1" to='' >Home</Link>
        <Link class="navbar-item2" to='/seminarhalls' >Seminar Halls</Link>
        <Link class="navbar-item2" to='/hallcheck' >Halls Checking</Link>
        <Link class="navbar-item3" to='/login'><span>Login</span></Link>
        <Link class="navbar-item3" to='/signup'><span>SignUp</span></Link>
      </div>
    </div>
  )
}

export default Navbar

