import React from 'react'
import './hero.css'
import hall from './maharaja.jpeg'
import { Link } from 'react-router-dom'
import Feature from '../Features/Feature'
import Footer from '../Footer/Footer'
const Hero = () => {
    return (
        <div>
            <div className='hero'>
                <div className='ri'>
                    <div>
                        <h1 className='w'>WELCOME TO SEMINAR HALLS BOOKINGS</h1>
                    </div>
                    <div>
                        <p className='o'>Our platform makes it easy for you to book a seminar hall with just a few clicks.</p>
                    </div>
                    <div className='btnn'>
                        <Link to='/login' className='pp'><p className='b'>Start Booking</p></Link>
                    </div>
                </div>
                <div className='left'>
                    <img src={hall} className='img' style={{ 'width': '550px' }} />
                </div>


            </div>

            <Feature />
           
            <Footer/>

        </div>

    )
}

export default Hero