import React, { useState } from 'react';
import jsPDF from 'jspdf';
import './rep.css';
const Feedback = () => {
    const [formData, setFormData] = useState({
        faculty: '',
        eventcoordinator: '',
        chiefguest: '',
        aboutevent: ''
    });

    const handleChange = (event) => {
        setFormData({
            ...formData,
            [event.target.name]: event.target.value,
        });
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const pdf = new jsPDF();
        pdf.text(`Name: ${formData.faculty}\nEventcoordinator: ${formData.eventcoordinator}\nchiefguest: ${formData.chiefguest}\nAboutevent:${formData.aboutevent}`, 10, 10);
        pdf.save('form-data.pdf');
    };

    return (
        <div className='d1'>
            <form onSubmit={handleSubmit}>
                <h1 className='kk'>Feedback</h1>
                <div className='d2'>
                    <input
                        type="text"
                        name="faculty"
                        placeholder="Faculty Name"
                        value={formData.faculty}
                        onChange={handleChange}
                        className='oo'
                    />
                    <br /><br />
                    <input
                        type="text"
                        name="eventcoordinator"
                        placeholder="eventcoordinator"
                        value={formData.eventcoordinator}
                        onChange={handleChange}
                        className='oo'
                    />
                    <br /><br />
                    <input
                        type="text"
                        name="chiefguest"
                        placeholder="chiefguest"
                        value={formData.chiefguest}
                        onChange={handleChange}
                        className='oo'
                    />
                    <br /><br />
                    <textarea
                        name="aboutevent"
                        placeholder="About Event"
                        value={formData.aboutevent}
                        onChange={handleChange}
                        className='oop'
                    />
                    <br /><br />
                    <button type="submit">Submit </button>
                </div>
            </form>
        </div>
    );
};

export default Feedback;