import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom'
import './halls.css'
import summa from '../images/hallsumma.jpeg'
const Halls = () => {
    const [halls, setHalls] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const response = await axios.get('http://localhost:4000/seminarhall');
            setHalls(response.data);
        };
        fetchData();
    }, []);
    return (
        <div className='su'>
            <h1>Seminar Halls</h1>
            {halls.map((hall) => (
                <div className="card" key={hall._id}>
                    <div className='inner'>
                        <div className='left'>
                            <img src={summa} style={{ 'width': '250px', 'height': '150px', 'borderRadius': '20px', 'marginTop': '20px' }} />
                        </div>
                        <div className='right'>
                            <div className="card-header">
                                <h3 className='hallna'>{hall.hallname}</h3>
                            </div>
                            <div className="card-body">
                                <p className='c'>Capacity: {hall.capacity}</p>
                                <p className='c'>Location: {hall.location}</p>
                            </div>
                            <div className='bbtnn'>
                                <Link to='/booking'><p className='bookbtnn'>Book</p></Link>
                            </div>
                        </div>
                    </div>
                </div>
            ))


            }

        </div>
    );
};

export default Halls;
