import React from 'react'
import { Link } from 'react-router-dom'
import first from './book.jpeg'
import second from './audi.jpg'
import third from './semi.jpg'
import fourth from './s2.jpg'
import './feature.css'
const Feature = () => {
    return (
        <div>
            <h2 className='ce'>Our Features</h2>
            <div className='feature'>
                <div className='first'>

                    <div className='left'>
                        <img src={first} style={{ width: "500px", height: "500px" }} />
                    </div>
                    <div className='right1'>
                        <div>
                            <h1 className='e'>Easy Booking</h1>
                        </div>
                        <div>
                            <p className='ou'>Our platform makes it easy for you to book a seminar hall with just a few clicks. You can select the date, time, and location of your choice, and make a reservation in no time.</p>
                        </div>
                        <div className='bbb'>
                            <Link to='' className='r'><span className='rr'>Register</span></Link>
                        </div>
                    </div>

                </div>
                <div className='second'>
                    <div className='left1'>
                        <div>
                            <h1 className='e'>Customizable Spaces</h1>
                        </div>
                        <div>
                            <p className='ou'>Our halls are customizable to fit your needs, so you can choose the perfect setup for your event. Whether you need a lecture-style seating arrangement or a more relaxed, round-table setup, we've got you covered.</p>
                        </div>
                        <div className='bbb'>
                            <Link to='' className='r'><span className='rr'>Register</span></Link>
                        </div>

                    </div>
                    <div className='right'>
                        <img src={second} style={{ width: "500px", height: "500px" }} />

                    </div>

                </div>
                <div className='third'>
                    <div className='left'>
                        <img src={third} style={{ width: "500px", height: "500px" }} />
                    </div>
                    <div className='right1'>
                        <div>
                            <h1 className='e'>Advanced Technology</h1>
                        </div>
                        <div>
                            <p className='ou'>We are equipped with the latest technology to ensure a seamless experience for you and your guests. From high-speed Wi-Fi to multimedia systems and projectors, our halls are designed to support modern presentations and events.</p>
                        </div>
                        <div className='bbb1'>
                            <Link to='' className='r'><span className='rr'>Register</span></Link>
                        </div>


                    </div>



                </div>
                <div className='second'>
                    <div className='left1'>
                        <div>
                            <h1 className='e'>On-Site Support</h1>
                        </div>
                        <div>
                            <p className='ou'>Our team of professionals is available on-site to ensure a smooth and successful event. Whether you need help with setup, technical support, or any other needs, we are here to help.</p>
                        </div>
                        <div className='bbb'>
                            <Link to='' className='r'><span className='rr'>Register</span></Link>
                        </div>

                    </div>
                    <div className='right'>
                        <img src={fourth} style={{ width: "500px", height: "500px" }} />

                    </div>


                </div>


            </div>

        </div>

    )
}

export default Feature