import React from 'react'
import { Link } from 'react-router-dom'
import  './foo.css'

const Footer = () => {
    return (
        <div className='footer'>
            <div className='up'>
                <div >
                    <Link to='/feedback' className='thee'><p>Feedback</p></Link>
                </div>
            </div>
            <div className='down'>
                <p>Copyright &copy; All rights reserved</p>
                <p>Designed by IT Elites, 2023</p>

            </div>
        </div>
    )
}

export default Footer