import React, { useState } from 'react';
import axios from 'axios';

const InsertHall = () => {
    const [hallname, setHallname] = useState('')
    const [image, setimage] = useState(null)
    const [location, setlocation] = useState('')
    const [projector, setprojector] = useState(false)
    const [whiteboard, setwhiteboard] = useState(false)
    const [capacity, setcapacity] = useState('')

    const saveFile = ()=>{
        setimage(image.current.files[0]);
    
    }

    const handleSubmit = async (e) => {
        // const formData = new FormData();
        // formData.append("hallname", hallname);
        // formData.append("location",location);
        // formData.append("capacity",capacity);
        // formData.append("image", image);
        // formData.append("projector",projector);
        // formData.append("whiteboard",whiteboard);
        e.preventDefault();
        try {
            const response = await axios.post('http://localhost:4000/inserthall', {
                hallname,location,capacity,projector,whiteboard
            });
            alert("Successfully Create a New Seminar hall")
            console.log(response.data);
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <input
                type="text"
                placeholder="Hall Name"
                value={hallname}
                onChange={(e) => setHallname(e.target.value)}
                name='hallname'
            />
            <input
                type="text"
                placeholder="Capacity"
                value={capacity}
                onChange={(e) => setcapacity(e.target.value)}
                name='capacity'
            />
            <input
                type="text"
                placeholder="Location"
                value={location}
                onChange={(e) => setlocation(e.target.value)}
                name='location'
            />
            <input
                type="checkbox"
                checked={projector}
                onChange={(e) => setprojector(e.target.checked)}
                name='projector'
            /><label>Projector</label>
            <input
                type="checkbox"
                checked={whiteboard}
                onChange={(e) => setwhiteboard(e.target.checked)}
                name='whiteboard'
            /><label>whiteboard</label>
            {/* <input type='file'
                onChange={saveFile}
            /> */}
            <button type="submit">Add Hall</button>
        </form>
    );
};

export default InsertHall;

