import React, { useEffect, useState } from 'react'
import axios from 'axios'
import './books.css'
const BookingDetails = () => {
    const [book, setBook] = useState([]);


    useEffect(() => {
        const fetchData = async () => {
            const response = await axios.get('http://localhost:4000/api/booking-requests', null);
            console.log(response.data);
            setBook(response.data);
        };
        fetchData();
    }, []);

        const [success, setSuccess] = useState(false);

        const handleClick = (id) => {
            axios.delete(`http://localhost:4000/delete/${id}`)
                .then(res => setSuccess(res.data.success))
                .catch(err => console.log(err));
        };

    return (
        <div>
            <table className='table table-striped'>
                <thead>
                    <tr>
                        <th scope="col">HallName</th>
                        <th scope="col">Event Start</th>
                        <th scope='col'>Event End</th>
                        <th scope="col">Attendees</th>
                        <th scope="col">Event Name</th>
                        <th scope="col">Club Name</th>
                        <th scope="col">Status</th>
                        <th scope='col'>Delete</th>

                    </tr>
                </thead>
                <tbody>
                    {
                        book.map(hall => (
                            <tr key={hall._id}>
                                <td>{hall.hallname}</td>
                                <td>{hall.startAt}</td>
                                <td>{hall.endAt}</td>
                                <td>{hall.attendees}</td>
                                <td>{hall.eventname}</td>
                                <td>{hall.clubname}</td>
                                <td>{hall.status}</td>
                                <td className='btn btn-danger text-dark'  onClick={()=>handleClick(hall._id)}>Delete</td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>

        </div>
    )
}

export default BookingDetails