import React, { useState } from 'react';
import axios from 'axios';


const Book = ({ hall_id }) => {
    const [email, setemail] = useState('')
    const [clubname, setclubname] = useState('')
    const [hallname, setHallname] = useState('')
    const [eventname, seteventname] = useState('')
    const [startAt, setStartAt] = useState('')
    const [endAt, setEndAt] = useState('')
    const [attendees, setattendees] = useState('')
    const [phonenumber, setphoneno] = useState('')
    // const [image, setimage] = useState(null)

    const handleSubmit = async(e) => {

        try {
            e.preventDefault();
            await axios.post('http://localhost:4000/bookinghall', {
                clubname, eventname, endAt, startAt, attendees, phonenumber,email,hallname
            }).then(res => {
                if (res.data.message == "Hall successfully booked.") {
                    alert("Successfully Create a New Seminar hall")
                }
            })

        } catch (error) {
            console.error(error);
            alert(error);
        }
    };

    return (
        <div>
            <div className='background'>
                <div className='shape'></div>
                <div className='shape'></div>
            </div>
            <form onSubmit={handleSubmit} id="f1">
                <h3>BOOKING HALLS</h3>
                <input
                    type="text"
                    placeholder="HallName"
                    value={hallname}
                    onChange={(e) => setHallname(e.target.value)}
                    name='hallname'
                />

                <input
                    type="email"
                    placeholder="Email"
                    value={email}
                    onChange={(e) => setemail(e.target.value)}
                    name='email'
                />

                <input
                    type="text"
                    placeholder="Clubname"
                    value={clubname}
                    onChange={(e) => setclubname(e.target.value)}
                    name='clubname'
                />
                <input
                    type="text"
                    placeholder="Event name"
                    value={eventname}
                    onChange={(e) => seteventname(e.target.value)}
                    name='eventname'
                />

                Check in <input
                    type="datetime-local"
                    placeholder="Check in"
                    value={startAt}
                    onChange={(e) => setStartAt(e.target.value)}
                    name='checkin'
                /><br />
                Check out<input
                    type="datetime-local"
                    placeholder="Check out"
                    value={endAt}
                    onChange={(e) => setEndAt(e.target.value)}
                    name='checkout'
                /><br />
                <input
                    type="text"
                    placeholder="Attendees"
                    value={attendees}
                    onChange={(e) => setattendees(e.target.value)}
                    name='attendees'
                />
                <input
                    type="text"
                    placeholder="Phone number"
                    value={phonenumber}
                    onChange={(e) => setphoneno(e.target.value)}
                    name='phoneno'
                />


                <button type="submit">Book</button>
            </form>
        </div>
    );
};

export default Book;